import React from 'react';
import { Jumbotron } from 'react-bootstrap';
import HistCarousel from './HistCarousel';
/* import './../styles/History.css'; */


const History = () => (

    <section className="historySection">
    <Jumbotron className="History"> 
      <HistCarousel />
    </Jumbotron>
    <div className="supporting">
      <div className="containter-fluid">
        <div className="main">
          <h1 className='historyTitleEng'>Races Peruanas of Los Angeles - History</h1>
          <h6 className='historyCaption'>(Spanish Version at the bottom)</h6>
          <p>It was the love for the land of our birthplace and unsettled sentiments of continuance that filled our hearts when leaving our land for this land of greater opportunities, and which motivated our spirit to put together our popular customs, songs, music and traditional folkloric dances of our beloved Peru in far South America. Once done, we then assumed the great challenge to disseminate our culture through the execution of this blend of dances, music and culture.</p>
          <p>In 1989, the Espejo sisters Doris and Jeanine, who where then 6 and 4 years old, began to learn and practice the Peruvian dances under the direction of their dance teacher and choreographer Rosagna Sanchez during the first 8 years. Now, years later, Jeanine continues to learn and perform the native dances with enthusiasm and enjoyment. In 1995, Raices Peruanas grew stronger when Margott Hinostroza, Maria Luisa Alvarado, Julie Shimabuco, Maria Lilet Burmester, Walter Almora, Edwin Delgado, Jose Elorrieta, and Henry Aquije, became members and the pioneers of this dance group.</p>
          <p>It was through the effort of the Espejo family then, that the group Races Peruanas was established in Southern California, leaving audiences at awe for the beauty and professionalism they portray at each of this groups performances, uniting people and hearts in this multicultural land.</p>
          <p>Doris and Jeanine were the first children to participate in this group, and later in 1996, at age 4, their younger sister April joined them. The three sisters are all U.S. born of Peruvian parents. Currently other young people of the Peruvian community in Los Angeles are also part of the group, promoting thus the participation of youngsters in art and culture.</p>
          <h1 className='historyTitleEsp'>Races Peruanas de Los Angeles - Historia</h1>
          <p>Era el amor a la tierra que nos vio nacer y a los sentimientos de inquietud que llenaba nuestros corazones cuando dejamos nuestra querida patria para venir a este pas de grandes oportunidades, que nos motiv a juntar nuestra msica, canciones, bailes tradicionales, trajes tpicos, y el folclor de nuestro amado Per. Entonces fue ah que asumimos el gran reto de difundir nuestra cultura a travs de las danzas y msica.</p>
          <p>En 1989, las hermanitas Doris y Jeanine Espejo, que en ese tiempo tenan 6 y 4 aitos, empezaron a aprender y a practicar los bailes tradicionales peruanos bajo la direccin de su profesora de baile y coregrafa Rosagna Sanchez durante los primeros 8 aos. Ahora, muchos aos despus, Jeanine contina aprendiendo y actuando estos bailes nativos con mucho gozo y entusiasmo. En 1995, esta institucin cultural se fortaleci cuando Margott Hinostroza, Maria Luisa Alvarado, Julie Shimabuco, Maria Lilet Burmester, Walter Almora, Edwin Delgado, Jose Elorieta, y Henry Aquije se hicieron integrantes del grupo convirtindose as en los pioneros de Races Peruanas.</p>
          <p>Fue a travs del esfuerzo de la familia Espejo que el grupo Races Peruanas fue establecido en el sur de California dejando a la audiencia pasmados por la belleza y profesionalismo al desempear cada una de sus actuaciones, uniendo a la gente y a corazones de este pas multicultural.</p>
          <p>Doris y Jeanine fueron las primeras nias en participar en este grupo, y despus en 1996, a los 4 aos de edad, su hermana menor April se uni al grupo. Las tres hermanas son nacidas en EE.UU. de padres peruanos. Actualmente otros nios y jvenes de la comunidad peruana de Los ngeles son tambin miembros de este grupo, promoviendo as la participacin de jvenes y nios en el arte y cultura.</p>         
        </div>
      </div>
    </div>
    </section>
);

export default History;



